$black: rgba(14, 13, 23, 1);
$white: rgba(255, 255, 255, 1);
$gray: rgba(217, 217, 217, 1);
$purple: rgba(101, 60, 244, 1);

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  // outline: 1px solid red;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-style: normal;
  position: relative;
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  color: $black;
  user-select: none;
  cursor: pointer;
}

button {
  user-select: none;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.container {
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
  padding-left: 3.75vw;
  padding-right: 3.75vw;
}

.nowrap {
  white-space: nowrap;
}

.button-fill-hover {
  display: flex;
  padding: 10px 20px;
  align-items: center;
  gap: 8px;
  border-radius: 12px;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: -0.28px;
  position: relative;
  transition: background-color 0.6s ease;
  overflow: hidden;
  .text,
  .dot {
    position: relative;
    z-index: 2; //На 1 больше текущего слоя
  }
  &:after {
    content: '';
    position: absolute;
    z-index: 1; //На текущем слое
    width: 0;
    height: 0;
    top: var(--mouse-y);
    left: var(--mouse-x);
    transform: translate(-50%, -50%);
    border-radius: 100%;
    transition: width 0.4s ease, height 0.4s ease;
  }
  &:hover {
    &:after {
      width: 400px;
      height: 400px;
    }
  }
}

